var Query = require('./Query');

var Affiliate = function (affiliateId) {
    if (typeof MutationObserver === 'undefined') {
        this.attach = ()=>{};
        return console.error('No support for MutationObserver');
    }
    var amazonURLs = ['amazon.com', 'www.amazon.com'];

    var traverseNodes = function (nodeSet) {
        Object.cycle(nodeSet, function (node) {
            if (typeof node !== 'object' || node === null) return;
            if (node.childNodes[0]) traverseNodes(node.childNodes);
            if (amazonURLs.indexOf(node.hostname) !== -1) amazonify(node);
        });
    };

    var amazonify = function (node) {
        var url = (String(node.href) || '').split('?');
        var preQuery = (url[1] || '').split('&');
        var query = {};
        for (var i in preQuery) {
            var keyVal = preQuery[i].split('=');
            if (keyVal[0]) query[keyVal[0]] = decodeURIComponent(String(keyVal[1])) || '';
        }
        query.tag = affiliateId;
        url[1] = Query(query);
        node.href = url.join('?');
    };

    this.observer = new MutationObserver(function(mutations) {
	    Object.cycle(mutations, function(mutation) {
            traverseNodes(mutation.addedNodes);
	    });
    });

    var observerConfig = {
        childList: true,
        attributes: true
    };

    this.attach = function () {
        if (document.readyState === 'complete') traverseNodes(document.body.childNodes);
        this.observer.observe(document.body, observerConfig);
    }.bind(this);
};

module.exports = Amazon;


